$(function(){
  $("#pop").css("display","none")

$(".infobtn").click(function(){
  $("#pop").fadeToggle('fast');
})

$(document).click(function(){
  $("#pop").fadeOut('fast');
});

$(".infobtn").click(function(event){
  event.stopPropagation(); // 이벤트 전파 방지(버블링방지)
});

})


